let l = document.getElementById("lk");
let sf = document.getElementById("s");
var g = document.getElementById("fufa");

sf.onclick = function () {
  l.classList.add("k");
  l.classList.remove("mk");
  g.remove();

  var r = document.createElement("h2");
  r.textContent = " اش دراني";
  r.style.fontSize = "9em";
  r.style.textAlign = "center";
  r.style.paddingTop = "210px";
  l.appendChild(r);
};
